import { Component, h, Prop, State, Method, Watch } from '@stencil/core';

/** @deprecated Ks-image is deprecated. Use ks-img instead. */

@Component({
	tag: 'ks-image'
})
export class Image {
    @Prop() src: string;
    @Prop() alt: string;

    @Prop() width: string;
    @Prop() height: string;

    @Prop() sync: boolean = false;
    @Prop() cover: boolean = false;
    @Prop() contain: boolean = false;

    @State() loaded: boolean = false;
    @State() loadAnimated: boolean = false;
    
    @Method() async loadCheck(e: Event) {
        if (e.target instanceof HTMLElement && !e.target.getAttribute("src").includes('data:image'))
            this.loaded = true;
    }

    componentDidUpdate() {
        if (this.loaded && !this.loadAnimated) {
            setTimeout(() => {
               this.loadAnimated = true;
            }, 1000);
        }
    }

    @Watch('src') srcListener() {
        this.loaded = false;
        this.loadAnimated = false;
    }

	render() {
        let imageStyle = "";

        if (!this.loaded)
            imageStyle = "uk-invisible uk-position-center";
            
        else if (!this.loadAnimated) 
            imageStyle = "uk-animation-fade"
        
        if(this.sync)
            return <img src={this.src} width={this.width} height={this.height} alt={this.alt}/>;

        else if(this.cover) return (
            <div class={"uk-cover-container"}>
                <canvas width={this.width} height={this.height}></canvas>
                {!this.loaded ? <div class="ks-spinner uk-position-center"></div> : null}
                <img uk-cover onLoad={e => this.loadCheck(e)} class={imageStyle} width={this.width} height={this.height} data-src={this.src} alt={this.alt} uk-img="target: !ks-image"/>
            </div>
        );

        else if(this.contain) return (
			<div class={"uk-position-relative"}>
                <canvas width={this.width} height={this.height}></canvas>
                {!this.loaded ? <div class="ks-spinner uk-position-center"></div> : null}
                <img onLoad={e => this.loadCheck(e)} class={imageStyle + " uk-position-center uk-responsive-height"} data-src={this.src} width={this.width} height={this.height} alt={this.alt} uk-img="target: !ks-image"/>
            </div>
		);

        else return (
			<div class={"uk-position-relative"}>
                {!this.loaded ? [
                    <canvas width={this.width} height={this.height}></canvas>,
                    <div class="ks-spinner uk-position-center"></div>
                ]: null}
                <img onLoad={e => this.loadCheck(e)} class={imageStyle} data-src={this.src} width={this.width} height={this.height} alt={this.alt} uk-img="target: !ks-image"/>
            </div>
		);
	}
}
